import request from '../utils/request'

// 获取所有班级信息
export const getAllGradeMsg = () => {
    return request('/manger/grade')
}

export const getOtherGrade = () => {
    return request('/manger/grade/new')
}

export const getAllRoom = () => {
    return request('/manger/room')
}

export const addGrade = (payload) => {
    return request('/manger/grade', {
        method:'POST',
        body:payload
    })
}

export const delGrade = (payload) => {
    return request('/manger/grade/delete', {
        method:'DELETE',
        body:payload
    })
}

export const editGrade = (payload) => {
    return request('/manger/grade/update', {
        method: 'PUT',
        body: payload
    })
}


export const addRoom = (payload) => {
    return request('/manger/room', {
        method: 'POST',
        body: payload
    })
}


//删除教室
export const delRoom = (payload) => {
    return request('/manger/room/delete', {
        method: 'DELETE',
        body: payload
    })
}

// 获取学生信息
export const getAllStudents = () => {
    return request('/manger/student')
}

export const getOtherStudents = () => {
    return request('/manger/student/new')
}


export const delStudent = ({student_id}) => {
    return request(`/manger/student/${student_id}`, {
        method: 'DELETE'
    })
}

