import {
    getAllStudents,
    getAllRoom,
    delStudent,
    getOtherStudents,
    getAllGradeMsg
} from '@/service/questManger'

import {
    getAllSubject
} from '@/service/questionsManger'

function allData() {
    return Promise.all([getAllRoom(), getAllSubject()])
}

function allStudents() {
    return Promise.all([getAllStudents(), getOtherStudents(), getAllGradeMsg()])
}

export default {
    namespace:'student',
    state:{
        foundationData: [],
        allStudents: [],
        paginationMsg: {
            current: 1,
            pageSize: 20,
            showSizeChanger: true
        },
        allRoom: [],
        allSubject: [],
        allGrade: []
    },
    effects: {
        *delStudent({payload}, {put, call}) {
            yield call(delStudent, payload);
            yield put({
                type: 'getAllStudent'
            })
        },
        *getOtherData(_, {put, call}) {
            let res = yield call(allData);
            yield put({
                type: 'saveOtherData',
                payload: {
                    allRoom: res[0].data,
                    allSubject: res[1].data
                }
            })
        },
        *getAllStudent(_, {put, call}) {
            let res = yield call(allStudents);
            yield put({
                type: 'saveFoundationData',
                payload: res[0].data.concat(res[1].data)
            })
            yield put({
                type: 'saveStudent',
                payload: res[0].data.concat(res[1].data)
            })
            yield put({
                type: 'saveGrade',
                payload: res[2].data
            })
            
        },
        *searchContent({ payload }, { put, select }) {
            let allKeys = ['student_name', 'room_id', 'grade_id']
            let allData = yield select((state) => {
                return state.student.foundationData
            })
            let allPagination = select((state) => {
                return {
                        ...state.student.paginationMsg,
                        current: 1,
                        pageSize: 20,
                    }
            })
            yield put({
                type: 'changePagination',
                payload: allPagination
            })
            let res = allData.filter(val => {
                return payload[allKeys[0]].trim() ? payload[allKeys[0]].trim() === val[allKeys[0]].trim() : true
                && payload[allKeys[1]] ? payload[allKeys[1]] === val[allKeys[1]] : true
                && payload[allKeys[2]] ? payload[allKeys[2]] === val[allKeys[2]] : true
            })
            yield put({
                type: 'saveStudent',
                payload: res
            })
        }
    },
    reducers: {
        saveFoundationData(state, {payload}) {
            return {
                ...state,
                foundationData: payload
            }
        },
        saveStudent(state, {payload}) {
            return {
                ...state,
                allStudents: payload
            }
        },
        saveOtherData(state, {payload}) {
            return {
                ...state,
                ...payload
            }
        },
        changePagination(state, {payload}) {
            return {
                ...state,
                paginationMsg: {...payload}
            }
        },
        saveGrade(state, {payload}) {
            return {
                ...state,
                allGrade: payload
            }
        }
    }
}